.container{
    height:100vh;
    position:relative;
}

.container .sidebar{
    position:fixed;
    background-color: #1c1c1c;
    margin-top: 30px;
    height:100vh;
    width:90px;
    z-index:10;
    text-align:center;
    color: #efefef;    
}



.container .sidebar2{
  padding: 10px;
  
  position:fixed;
  background-color: #161616;
  margin-top: 30px;
  margin-left: 90px;
  height:100vh;
  width:300px;
  z-index:10;
  text-align:left;
  color: gray;
}
.sidebar li:nth-child(1){
    padding:30px 0px;
}


.sidebar li:hover:nth-child(1){
    padding:30px 0px;
    background-color:transparent;
}

.sidebar li{
    
    padding:12px;
    cursor:pointer;
    transition:all 0.5s;
    letter-spacing:1px;
    display:flex;
    flex-direction:column;
}

.sidebar2 li{
  
  padding:12px;
  cursor:pointer;
  transition:all 0.5s;
  letter-spacing:1px;
  display:flex;
  flex-direction:column;
}


.sidebar li span:nth-child(1){
    font-size:20px;
}

.sidebar li span:nth-child(2){
    font-size:10px;
}

.sidebar li:hover{
    color:#f2f2f2;
    padding:12px;
     
}

.sidebar2 li:hover{
  color:#f2f2f2;
  background-color: #2b2b2b;
  padding:12px;
   
}

.container .content{
    padding-top: 30px;
    color: gray;
}

.container.with-sidebar .content  {
  text-align: center;
  margin-left: 300px;
}

.container.without-sidebar .content  {
